import { gql } from 'apollo-boost';

export const SEND_PUSH = gql`
  # mutation sendPush($endpoint: String, $p256dh: String, $auth: String) {
  #   sendPush(endpoint: $endpoint, p256dh: $p256dh, auth: $auth)
  # }
  mutation sendPush(
    $endpoint: String!
    $auth: String!
    $p256d: String!
    $message: String!
  ) {
    sendPush(endpoint: $endpoint, auth: $auth, p256d: $p256d, message: $message)
  }
`;

export const SUBSCRIBE_ALARM = gql`
  mutation subscribeAlarm(
    $endpoint: String!
    $auth: String!
    $p256dh: String!
  ) {
    subscribeAlarm(endpoint: $endpoint, auth: $auth, p256dh: $p256dh)
  }
`;

export const UPDATE_PUSHDATA = gql`
  mutation updatePushData(
    $isSubscribe: Boolean!
    $endpoint: String
    $auth: String
    $p256dh: String
  ) {
    updatePushData(
      isSubscribe: $isSubscribe
      endpoint: $endpoint
      auth: $auth
      p256dh: $p256dh
    )
  }
`;

export const SCHEDULE_INITALARM = gql`
  mutation scheduleInitAlarm {
    scheduleInitAlarm
  }
`;

export const UNSUBSCRIBE_ALARM = gql`
  mutation unsubscribeAlarm {
    unsubscribeAlarm
  }
`;

export const UPDATE_SCHEDULE = gql`
  mutation updateSchedule($questionId: String!, $interval: String!) {
    updateSchedule(questionId: $questionId, interval: $interval)
  }
`;
