import React, { useState, useEffect } from 'react';
import AlarmPresenter from './AlarmPresenter';
import urlB64ToUint8Array from 'util/base64';
import useInput from 'Hooks/useInput';
// import webpush from 'web-push';
import { useQuery, useMutation } from '@apollo/react-hooks';
import {
  SCHEDULE_INITALARM,
  UPDATE_PUSHDATA,
  UNSUBSCRIBE_ALARM,
  UPDATE_SCHEDULE,
} from './AlarmQueries';
import { GET_QUESTIONSOFME } from 'Routes/Diary/DiaryQuries';
import Loader from 'Components/Loader';
import { READ_MYDATA } from 'sharedQueries';
//import toast from 'react-toastify';

export default () => {
  console.log('alarm');
  const pushInput = useInput('');
  const [isSubscribed, setIsSubscribed] = useState(false);
  console.log('isSubscribed : ', isSubscribed);
  const { data: myQuestionData, loading: myQuestionLoading } = useQuery(
    GET_QUESTIONSOFME
  );
  const { data: myData, loading: myDataLoading } = useQuery(READ_MYDATA);

  const [scheduleInitAlarmMutation] = useMutation(SCHEDULE_INITALARM);

  const [unsubscribeAlarmMutation] = useMutation(UNSUBSCRIBE_ALARM);

  const [updateScheduleMutation] = useMutation(UPDATE_SCHEDULE);

  const [updatePushDataMutation] = useMutation(UPDATE_PUSHDATA, {
    refetchQueries: () => [{ query: READ_MYDATA }],
    //onCompleted: scheduleInitAlarmMutation,
  });

  useEffect(() => {
    navigator.serviceWorker.ready.then(registration =>
      registration.pushManager.getSubscription().then(subscription => {
        if (subscription !== null) {
          const subscriptionToString = JSON.stringify(subscription);
          console.log('subscriptionToString : ', subscriptionToString);
          const subscriptionToJSON = JSON.parse(subscriptionToString);
          console.log('subscriptionToJSON : ', subscriptionToJSON);
          updatePushDataMutation({
            variables: {
              isSubscribe: true,
              endpoint: subscriptionToJSON.endpoint,
              auth: subscriptionToJSON.keys.auth,
              p256dh: subscriptionToJSON.keys.p256dh,
            },
          });
          //console.log('subscriptionToJSONkyes : ', subscriptionToJSON.keys);
        } else {
        }
        return !(subscription === null);
      })
    );
    // .then(result => {
    //   if (typeof result === undefined) {
    //   } else {
    //     setIsSubscribed(result);
    //   }
    // });
  }, [myDataLoading, myQuestionLoading, updatePushDataMutation]);

  //console.log('subscription : ', subscriptionForSend);

  //console.log('isSubscribed : ', isSubscribed);

  const applicationServerKey = urlB64ToUint8Array(
    'BJOsTa5vJrAqQ7gSvPp-4RxSQAsmp32_xm3m-umN751YNxW8BCu1166_33r0LXIxTHb_0KrYIqKGDTAe10WR5UA'
  );
  const subscribeUser = () => {
    //console.log('subscribeUser');
    return navigator.serviceWorker.ready.then(registration =>
      registration.pushManager
        .subscribe({
          userVisibleOnly: true,
          applicationServerKey: applicationServerKey,
        })
        .then(subscription => {
          console.log('');
          const subscriptionToString = JSON.stringify(subscription);
          //console.log('subscriptionToString : ', subscriptionToString);
          const subscriptionToJSON = JSON.parse(subscriptionToString);
          console.log('subscriptionToJSON : ', subscriptionToJSON);
          //console.log('subscriptionToJSONkyes : ', subscriptionToJSON.keys);
          updatePushDataMutation({
            variables: {
              isSubscribe: true,
              endpoint: subscriptionToJSON.endpoint,
              auth: subscriptionToJSON.keys.auth,
              p256dh: subscriptionToJSON.keys.p256dh,
            },
          }).then(() => scheduleInitAlarmMutation());

          //console.log('User is subscribed. subscription : ', subscription);
          //updateSubscriptionOnServer(subscription);
          // subscribeAlarmMutation({
          //   variables: {
          //     endpoint: subscriptionToJSON.endpoint,
          //     auth: subscriptionToJSON.keys.auth,
          //     p256dh: subscriptionToJSON.keys.p256dh,
          //   },
          // });
        })
        .catch(e => console.log(e))
    );
  };

  const unsubscribeUser = () => {
    //console.log('unsubscribeUser');
    //   
    return navigator.serviceWorker.ready.then(registration =>
      //  
      registration.pushManager
        .getSubscription()
        .then(subscription => {
          if (subscription) {
            // 
            console.log('');
            subscription.unsubscribe().then(() => {
              updatePushDataMutation({
                variables: {
                  isSubscribe: false,
                },
              }).then(() => unsubscribeAlarmMutation());
            });
          }
        })
        .catch(e => console.log(e))
    );
  };

  const updateSubscribe = async () => {
    if (isSubscribed) {
      await unsubscribeUser();
    } else {
      await subscribeUser();
    }
  };

  // const sendPush = async () => {
  //   const vapidKeys = webpush.generateVAPIDKeys();

  //   await webpush.setGCMAPIKey(
  //     'AAAANiqBhss:APA91bE3gamSqbcI3UErhK8wO-QlHwfefPsHiG8O5QQv1fU5GqKvEHOCT3vYD3iXY9R_ByxTTOw6qazWLxaxlLX64WnbyoOeLMGkWCgLpSOhQRlaLG86hoKDIX85sQY0iwKgHe0TICNE'
  //   );
  //   await webpush.setVapidDetails(
  //     'mailto:abhidhamma91@gmail.com',
  //     vapidKeys.publicKey,
  //     vapidKeys.privateKey
  //   );
  //   await webpush
  //     .sendNotification(subscription)
  //     .then(() => {
  //       console.log(
  //         'Push Application Server - Notification sent to ' +
  //           subscription.endpoint
  //       );
  //     })
  //     .catch(error => {
  //       console.log(error);
  //     });
  // };

  if (myQuestionLoading || myDataLoading) {
    return <Loader />;
  } else {
    console.log('myQuestionData', myQuestionData);
    console.log('myData', myData);

    return (
      <AlarmPresenter
        myData={myData}
        isSubscribed={isSubscribed}
        setIsSubscribed={setIsSubscribed}
        updateSubscribe={updateSubscribe}
        pushInput={pushInput}
        updateScheduleMutation={updateScheduleMutation}
        // subscribeAlarmMutation={subscribeAlarmMutation}
        myQuestionData={myQuestionData}
      />
    );
  }
};
