import React, { useEffect } from 'react';
import styled from 'styled-components';
import Tab from '../../Components/Tab';
import Switch from 'react-switch';
import { Success } from '../../Components/Icons';
import BlackOutLineButton from 'Components/BlackOutLineButton';
import { useHistory } from 'react-router-dom';
import './Alarm.css';
import { TimePicker } from 'antd';
import parser from 'cron-parser';
import moment from 'moment';
import { toast } from 'react-toastify';

const Wrapper = styled.div`
  display: grid;
  grid-template-rows: 7vh 7vh 15vh 15vh 15vh 15vh 15vh 15vh 15vh 15vh 15vh;
  height: 100vh;
`;

const Alarm = styled.div`
  display: grid;
  grid-template-columns: 6fr 4fr;
  grid-gap: 2vw;
  background: white;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.05);
  font-size: 1rem;
  align-items: center;
  width: 100%;
  height: 100%;
`;
const AlarmText = styled.div`
  display: grid;
  align-self: center;
  justify-self: center;
  font-size: 1rem;
`;
const ContinueButtonWapper = styled.div`
  width: 100%;
  height: 100%;
`;
const Empty = styled.div``;
const SwitchWrapper = styled.div`
  display: grid;
  align-self: center;
`;
const Title = styled.div`
  display: grid;
  grid-template-columns: 2fr 4fr 2fr;
  background: ${props => props.theme.activeColor};
  width: 100%;
  height: 100%;
`;
const TitleText = styled.div`
  font-family: 'KakaoB';
  font-size: 1.3rem;
  display: grid;
  align-self: center;
  justify-self: center;
  color: white;
`;
// const PushInputWrapper = styled.div`
//   display: grid;
//   align-items: center;
//   justify-items: center;
//   height: 16vh;
//   width: 100%;
//   background: white;
// `;
const TimePickerWrapper = styled.div`
  position: relative;
  display: grid;
  width: 100%;
  align-items: center;
  justify-items: center;

  > span {
    width: 100%;

    display: grid;
    align-items: center;
    justify-items: center;
  }

  > span > input {
    padding-left: 7vw;
    height: 5vh;
    font-size: 1rem;
    font-weight: 600;
    font-family: 'KakaoL';
    border: 1.5px solid ${props => props.theme.buttonColor};
    ::placeholder {
      color: ${props => props.theme.buttonColor};
    }
  }
  > span > span > i {
    display: grid;
    align-items: center;
  }

  > span > span > i > svg {
    color: ${props => props.theme.buttonColor};
  }
`;
const AlarmHandlerWapper = styled.div`
  display: grid;
  grid-template-rows: 5fr 5fr;
  height: 100%;
  align-items: center;
`;

export default ({
  subscribeAlarmMutation,
  myData,
  setIsSubscribed,
  isSubscribed,
  updateSubscribe,
  myQuestionData,
  updateScheduleMutation,
}) => {
  useEffect(() => {
    setIsSubscribed(myData.readMyData.isSubscribe);
  });
  let history = useHistory();

  const onTimepickerChange = async (time, questionId, type) => {
    const hours = moment(time).toObject().hours;
    const minutes = moment(time).toObject().minutes;
    const interval = `${minutes} ${hours} * * *`;
    //const interval = `10 * * * * *`;
    await updateScheduleMutation({
      variables: { questionId, interval },
    })
      .then(() => {
        toast.success(`${type} ${hours} ${minutes} `);
      })
      .catch(e => console.log(e));
  };

  return (
    <>
      <Wrapper>
        <Tab />
        <Title>
          <Empty />
          <TitleText></TitleText>
          <SwitchWrapper>
            <Switch
              onChange={updateSubscribe}
              checked={isSubscribed}
              onColor="#07C50E"
              uncheckedIcon={false}
              checkedIcon={false}
              boxShadow="0px 1px 5px rgba(0, 0, 0, 0.6)"
              activeBoxShadow="0px 0px 1px 10px rgba(0, 0, 0, 0.2)"
              // eslint-disable-next-line
              checkedIcon={<Success />}
            />
          </SwitchWrapper>
        </Title>
        {myQuestionData.getQuestionsOfMe.map(question => {
          const interval = parser
            .parseExpression(question.defaultInterval)
            .next()
            .toString();

          return (
            <Alarm key={question.id}>
              <AlarmText>{question.type}</AlarmText>

              <AlarmHandlerWapper>
                <TimePickerWrapper>
                  <TimePicker
                    // disabled={isSubscribed}
                    defaultValue={moment(interval)}
                    defaultOpenValue={moment(interval)}
                    placeholder={' '}
                    use12Hours
                    format="h:mm A"
                    onChange={(time, _) =>
                      onTimepickerChange(time, question.id, question.type)
                    }
                  />
                </TimePickerWrapper>
                <ContinueButtonWapper>
                  <BlackOutLineButton
                    width={'100%'}
                    onClick={() =>
                      history.push(`/singlequestion/${question.id}`)
                    }
                    text={' '}
                  />
                </ContinueButtonWapper>
              </AlarmHandlerWapper>
            </Alarm>
          );
        })}
        {/* <PushInputWrapper>
          <Life />
        </PushInputWrapper>
        <PushInputWrapper>
          <BlackOutLineButton
            width={'80vw'}
            text="     ?"
            onClick={async () => await sendPushMutation()}
          />
        </PushInputWrapper> */}
      </Wrapper>
    </>
  );
};
